@include font('helvetic-font', helveticaneue);
@include font-param('helvetic-font', helvetic_neue_bold, bold);

@include font('myriadpro-font', myriadpro-regular);
@include font-param('myriadpro-font', myriadpro-bold, bold);

@include font('opensans-font', opensans-regular);
@include font-param('opensans-font', opensans-bold, bold);

@mixin helvetic-font {
	font-family: helvetic-font, sans-serif; }

@mixin myriadpro-font {
	font-family: myriadpro-font, sans-serif; }

@mixin opensans-font {
	font-family: opensans-font, sans-serif; }

body {
	@include myriadpro-font;
	background-color: $bg-color; }

.container {
	max-width: 1170px;
	margin: 0 auto; }

